.Text-block {
    font-family: 'Droid Serif', Serif, serif;
    font-size:      calc(3px + 2vmin);
    max-width: 100vh;
    margin:    10px 0;
}

@media screen and (max-width: 600px)
{
    .Text-block {
        font-size: calc(10px + 2vmin);
        max-width: 50vh;
    }
}

@media screen and (max-height: 450px)
{
    .Text-block {
        font-size: calc(10px + 2vmin);
        max-width: 150vh;
    }
}