.Menu-container {
    display: flex;
    flex-direction: column;
    align-items: self-start;
    color: white;
    padding: 15px;
    position: absolute;
    top: 0;
    right: 0;
    font-size: calc(10px + 2vmin);
}

.Menu-item:hover{
    cursor: pointer;
    color:  #97caee;
}

.Menu-item.active {
    padding-bottom: 5px;
    border-bottom: 3px solid #477b9f;
}