.Contacts p {
    margin: 10px;
}

.Contacts a
{
    color: #477b9f;
}

.contact-social
{
    width: 30px;
}

@media screen and (min-height: 450px) {
    .Contacts
    {
        position:   absolute;
        bottom:     0;
        margin-top: 150px;
    }
}

@media screen and (max-width: 600px)
{
    .Contacts
    {
        position: initial;
        margin-top: 50px;
    }
}